import {QueryOperator} from '../enums';

export interface Query {
  // query regex string
  query: string;
  // which fields we're searching for, default: [name, meta.value]
  queryFields?: string[];
  // logical operator between fields, default: or
  queryOperator?: QueryOperator;
}
